.ContactMe {
    background: #191919;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0px 28px 28px 28px;;
    margin: 0px;
    height: 540px;
    width: 100%;
    position: relative;
    z-index: 0;

}



.ContactContent {
    z-index: 3;
    height: 540px;
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: top;

}

.Title {
    font-size: 3rem;
}

input {
    height:3rem;
    width: 540px;
    border-radius: 12px;
    -webkit-appearance: none; 
    -moz-appearance: none; 
    appearance: none;
    background-clip: padding-box;
    box-shadow: none !important;
    border:none;
    outline:none;
    padding: 1rem;
    box-sizing: border-box;
    font-size:12pt;
    margin: 8px;
    z-index: 7;
}


textarea {
    height:120pt;
    width: 540px;
    border-radius: 12px;
    -webkit-appearance: none; 
    -moz-appearance: none; 
    appearance: none;
    background-clip: padding-box;
    box-shadow: none !important;
    border:none;
    outline:none;
    padding: 1rem;
    box-sizing: border-box;
    font-size:12pt;
    margin: 8px;
    z-index: 7;
}

@media screen and (max-width: 768px) {

    .Title {
        font-size: 1.5rem;
    }
    input {
        width: 100%;
    }

    textarea{
        width:100%;
    }

}
